
import axios from 'axios';

// notepage
const axiosPostSavedChanges = value => {
    return axios.post('http://localhost:5000/notes/savedchanges', value)
};
const axiosCheckTitleDuplicate = (title) => {
    return axios.get('http://localhost:5000/notes/checktitleduplicate', { params: { title } })
}
const axiosUpdateTitle = async (title, prevTitle, username) => {
    return axios.post('http://localhost:5000/notes/updatetitle', { title, username, prevTitle });

};
const axiosCreateNewPost = passData => {
    return axios.post('http://localhost:5000/notes/createnewnote', passData)
}
const axiosFindSectionPage = (header, username) => {
    return axios.get('http://localhost:5000/notes/findsectionpage', { params: { header, username } })
}

// login
const axiosCheckLogin = values => {
    return axios.get('http://localhost:5000/user/login', { params: values })
}

//sidebar
const axiosPostRemoveNote = (item, username) => {
    const passParam = {
        username,
        header: item
    };
    return axios.post('http://localhost:5000/notes/removeNote', passParam);
};
const axiosGetFavourites = (username) => {
    return axios.get('http://localhost:5000/notes/getfavourites', { params: { username } })
}

const axiosSetHeaders = username => {
    return axios.get('http://localhost:5000/notes/findheader', { params: { username } })
}

//signup 
const axiosCheckDuplicate = (values) => {
    return axios.get('http://localhost:5000/user/checkuser', { params: values });
}

const axiosPostSignup = (values) => {
    return axios.post('http://localhost:5000/user/createuser', values);
}

export { axiosPostSignup, axiosCheckDuplicate, axiosSetHeaders, axiosGetFavourites, axiosPostRemoveNote, axiosCheckLogin, axiosFindSectionPage, axiosCreateNewPost, axiosPostSavedChanges, axiosCheckTitleDuplicate, axiosUpdateTitle }